@media (min-width: 1115px) {
  .grid-container-resume-page {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-areas:
      "header header header header"
      "left-panel intro intro right-panel"
      "left-panel texts images  right-panel"
      "left-panel texts graph  right-panel"
      "bott bott bott bott";
    grid-template-rows: 5vh 15vh 38vh 40vh 2vh;
    grid-template-columns: 3vw 37vw 57vw 3vw;
    overflow-x: hidden;

    .header {
      grid-area: header;
      display: flex;
      justify-content: center;
      background-color: aqua;
    }
    .left-panel {
      grid-area: left-panel;
      display: flex;
      justify-content: center;
      background-color: antiquewhite;
    }
    .right-panel {
      grid-area: right-panel;
      display: flex;
      background-color: antiquewhite;
    }
    .intro {
      grid-area: intro;
      display: flex;
      justify-content: center;
      background-color: rgb(245, 145, 13);
    }
    .texts {
      grid-area: texts;
      display: flex;
      justify-content: center;
      background-color: rgb(16, 231, 131);
    }
    .images {
      grid-area: images;
      display: flex;
      justify-content: center;
      background-color: rgb(54, 57, 221);
    }
    .graph {
      grid-area: graph;
      display: flex;
      justify-content: center;
      background-color: rgb(39, 39, 39);
    }
    .bott {
      grid-area: bott;
      display: flex;
      background-color: rgb(219, 97, 235);
    }
  }
}
